.modal-overlay {
  position: fixed;
  inset: 0;
  background: rgba(0, 0, 0, 0.45);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 1000;
}

.modal {
  width: min(720px, 95vw);
  background: #fff;
  border: 1px solid #e5e7eb;
  border-radius: 12px;
  overflow: hidden;
  display: flex;
  flex-direction: column;
}

.modal-header {
  padding: 1rem 1.25rem;
  border-bottom: 1px solid #e5e7eb;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.modal-body {
  padding: 1rem 1.25rem;
  max-height: 60vh;
  overflow: auto;
}

.modal-footer {
  padding: 1rem 1.25rem;
  border-top: 1px solid #e5e7eb;
  display: flex;
  justify-content: flex-end;
  gap: 0.5rem;
}

.close {
  border: 1px solid #d1d5db;
  background: #f3f4f6;
  padding: 0.25rem 0.5rem;
  border-radius: 6px;
}

.btn {
  padding: 0.5rem 0.75rem;
  border-radius: 8px;
  border: 1px solid #d1d5db;
  background: #f3f4f6;
}

.btn.btn-primary {
  background: #2563eb;
  color: #fff;
  border: none;
}

.btn.btn-secondary {
  background: #fff;
  color: #374151;
}

.form-input {
  flex: 1;
  border: 1px solid #d1d5db;
  border-radius: 8px;
  padding: 0.5rem 0.75rem;
}

.search-row {
  display: flex;
  gap: 0.5rem;
}

.results {
  margin-top: 0.75rem;
  display: flex;
  flex-direction: column;
  gap: 0.5rem;
}

.result-row {
  display: flex;
  justify-content: space-between;
  border: 1px solid #e5e7eb;
  border-radius: 8px;
  padding: 0.5rem 0.75rem;
}

.result-row .name {
  font-weight: 600;
}

.result-row .sub {
  font-size: 0.8rem;
  color: #6b7280;
}

.cart {
  margin-top: 1rem;
  display: flex;
  flex-direction: column;
  gap: 0.5rem;
}

.cart-row {
  display: flex;
  justify-content: space-between;
  align-items: center;
  border: 1px solid #e5e7eb;
  border-radius: 8px;
  padding: 0.5rem 0.75rem;
}

.cart-row .qty {
  width: 6rem;
}

.cart-row .cost {
  width: 7rem;
  margin-left: 0.5rem;
}

.cart-row .icon {
  margin-left: 0.5rem;
  background: transparent;
  border: none;
  cursor: pointer;
}
